import { sha256 } from '@noble/hashes/sha256'
import { bytesToHex, hexToBytes, randomBytes } from '@noble/hashes/utils'
import { bech32m } from 'bech32'

import { ChessInstance } from '../components/ChessJsTypes'

import * as NIP01 from '../util/nostr/nip01'
import * as NostrEvents from '../util/nostr/events'
import { arrayEquals, hashWithSha256 } from '../util/utils'
import { Pgn, ValidFen, toValidFen, historyToMinimalPgn } from '../util/chess'

export const VALID_Rookr_ID_EXAMPLE: RookrId = 'Rookr1ncmkasntavrcj8ujv32a98236kgnx5a3cm9cl9kmqpjh0tgyg46qqsfhdp'

export const FEN_START_POSITION = 'rnbqkbnr/pppppppp/8/8/8/8/PPPPPPPP/RNBQKBNR w KQkq - 0 1'
export const Rookr_START_GAME_E_REF = bytesToHex(sha256(FEN_START_POSITION))

export const Rookr_MESSAGE_KIND: NIP01.Kind = 30 // fiatjaf said so.. "please do chess on kind 30"

export const Rookr_START_GAME_FILTER: NIP01.Filter = {
  '#e': [Rookr_START_GAME_E_REF],
  kinds: [Rookr_MESSAGE_KIND],
}

export const RookrPrivateStartGameRef = (publicKey: NIP01.PubKey) => {
  const publicKeyHashed = hashWithSha256(publicKey)
  return hashWithSha256(publicKeyHashed + Rookr_START_GAME_E_REF)
}

export const createPrivateGameStartFilter = (publicKey: NIP01.PubKey): NIP01.Filter => {
  return {
    '#e': [RookrPrivateStartGameRef(publicKey)],
    kinds: [Rookr_MESSAGE_KIND],
  }
}

export const createGameFilterByGameId = (gameId: NIP01.EventId): NIP01.Filter[] => {
  return [
    {
      ids: [gameId],
      kinds: [Rookr_MESSAGE_KIND],
    },
    {
      '#e': [gameId],
      kinds: [Rookr_MESSAGE_KIND],
    },
  ]
}

export const createGameChatFilterByGameId = (gameId: NIP01.EventId): NIP01.Filter[] => {
  return [
    {
      '#e': [gameId],
      kinds: [1],
    },
  ]
}

export enum KindEnum {
  Start = 0,
  Move = 1,
  Chat = 2,
}

export interface RookrProtoContent {
  version: '0'
  fen: string
  move: string
  history: string[]
}

export const Rookr_ID_PREFIX = 'Rookr'

export type RookrId = `${typeof Rookr_ID_PREFIX}1${string}`
type GameStartEvent = NIP01.Event
type MoveEvent = NIP01.Event

export interface RookrMove {
  source(): GameStartEvent
  event(): MoveEvent
  parent(): RookrMove | null
  children(): RookrMove[]
  addChild(child: RookrMove): boolean
  content(): RookrProtoContent
  fen(): ValidFen
  pgn(): Pgn
  isValidSuccessor(move: RookrMove): boolean
  isStart(): boolean
}

const _validStartFen = toValidFen(FEN_START_POSITION)

abstract class AbstractGameMove implements RookrMove {
  private _children: RookrMove[] = []

  abstract source(): GameStartEvent
  abstract event(): MoveEvent
  abstract parent(): RookrMove | null
  abstract content(): RookrProtoContent
  abstract fen(): ValidFen

  children(): RookrMove[] {
    return this._children
  }
  addChild(child: RookrMove): boolean {
    if (!this.isValidSuccessor(child)) {
      return false
    }
    this._children = [...this._children, child]
    return true
  }
  isValidSuccessor(move: RookrMove): boolean {
    const fenIsValid = this.fen().validMoves().contains(move.fen())
    return fenIsValid
  }
  isStart(): boolean {
    return this.parent() === null && this.fen() === _validStartFen && this.pgn() === ''
  }
  pgn(): Pgn {
    return historyToMinimalPgn(this.content().history || [])
  }
}

export class GameStart extends AbstractGameMove {
  private _event: GameStartEvent
  private _content: RookrProtoContent
  constructor(event: GameStartEvent) {
    super()
    if (!isStartGameEvent(event)) {
      throw new Error('GameStartMoveEvent can only be created from a GameStartEvent')
    }
    this._event = event
    this._content = JSON.parse(event.content) as RookrProtoContent
  }
  source() {
    return this._event
  }
  event() {
    return this._event
  }
  parent(): null {
    return null
  }
  content(): RookrProtoContent {
    return this._content
  }
  fen(): ValidFen {
    // TODO: it is only supported to start from the start fen
    // future version might let you propose to start from any position
    return _validStartFen
  }
}

export class GameMove extends AbstractGameMove {
  private _event: NIP01.Event
  private _parent: RookrMove
  private _content: RookrProtoContent
  private _fen: ValidFen

  constructor(event: NIP01.Event, parent: RookrMove) {
    super()
    const content = JSON.parse(event.content) as RookrProtoContent

    // TODO: verify that 'move' is really valid (can be different to given fen!)
    if (content.history[content.history.length - 1] !== content.move) {
      throw new Error(`Invalid content: 'move' is not last entry of 'history'`)
    }
    if (!arrayEquals(parent.content().history, content.history.slice(0, content.history.length - 1))) {
      throw new Error('History does not match that of parent')
    }

    this._content = content

    this._parent = parent
    this._event = event
    this._fen = toValidFen(content.fen)

    if (!parent.isValidSuccessor(this)) {
      throw new Error('Cannot create from fen that is not a successor')
    }
  }
  source() {
    return this._parent.source()
  }
  event(): NIP01.Event {
    return this._event
  }
  parent(): RookrMove {
    return this._parent
  }
  content(): RookrProtoContent {
    return this._content
  }
  fen(): ValidFen {
    return this._fen
  }
}

const START_GAME_EVENT_PARTS: NIP01.EventInConstruction = (() => {
  const eventParts = NostrEvents.blankEvent()
  eventParts.kind = Rookr_MESSAGE_KIND
  eventParts.tags = [[NIP01.TagEnum.e, Rookr_START_GAME_E_REF]]
  return eventParts
})()

const RookrPrivateGameEventParts = (opponentPubKey: NIP01.PubKey): NIP01.EventInConstruction => {
  const eventParts = NostrEvents.blankEvent()
  eventParts.kind = Rookr_MESSAGE_KIND
  eventParts.tags = [[NIP01.TagEnum.e, RookrPrivateStartGameRef(opponentPubKey)]]
  return eventParts
}

const _constructStartGameEventWithParts = (
  pubkey: NIP01.PubKey,
  parts: NIP01.EventInConstruction
): NIP01.UnsignedEvent => {
  const eventParts = {
    ...parts,
    created_at: Math.floor(Date.now() / 1000),
    content: JSON.stringify({
      version: '0',
      kind: KindEnum.Start,
      history: [],
      nonce: bytesToHex(randomBytes(4)),
    }),
    pubkey,
  } as NIP01.EventParts
  return NostrEvents.constructEvent(eventParts)
}

export const constructPrivateStartGameEvent = (
  pubkey: NIP01.PubKey,
  opponentPubKey: NIP01.PubKey
): NIP01.UnsignedEvent => {
  return _constructStartGameEventWithParts(pubkey, RookrPrivateGameEventParts(opponentPubKey))
}

export const constructStartGameEvent = (pubkey: NIP01.PubKey): NIP01.UnsignedEvent => {
  return _constructStartGameEventWithParts(pubkey, START_GAME_EVENT_PARTS)
}

export const constructGameMoveEvent = (
  pubkey: NIP01.PubKey,
  startId: NIP01.EventId,
  headId: NIP01.EventId,
  game: ChessInstance
): NIP01.UnsignedEvent => {
  const history = game.history()
  const latestMove = (history && history[history.length - 1]) || null

  const eventParts = NostrEvents.blankEvent()
  eventParts.kind = Rookr_MESSAGE_KIND
  eventParts.pubkey = pubkey
  eventParts.created_at = Math.floor(Date.now() / 1000)
  eventParts.content = JSON.stringify({
    version: '0',
    kind: KindEnum.Move,
    fen: game.fen(),
    move: latestMove,
    history: history,
  })
  eventParts.tags = [
    [NIP01.TagEnum.e, startId],
    [NIP01.TagEnum.e, headId],
  ]
  return NostrEvents.constructEvent(eventParts)
}

const tryParseJsonObject = (val: string) => {
  try {
    return JSON.parse(val)
  } catch (e) {
    return null
  }
}

export const isStartGameEvent = (event?: NIP01.Event): boolean => {
  const json = (event && event.content && event.content.startsWith('{') && tryParseJsonObject(event.content)) || {}
  return (
    !!event &&
    event.kind === Rookr_MESSAGE_KIND &&
    //arrayEquals(event.tags, [[NIP01.TagEnum.e, Rookr_START_GAME_E_REF]]) &&
    json &&
    json.kind === KindEnum.Start &&
    arrayEquals(json.history, [])
  )
}

export const mightBeMoveGameEvent = (event?: NIP01.Event): boolean => {
  const json = (event && event.content && event.content.startsWith('{') && tryParseJsonObject(event.content)) || {}
  return (
    !!event &&
    event.kind === Rookr_MESSAGE_KIND &&
    json &&
    json.kind === KindEnum.Move &&
    Array.isArray(json.history) &&
    json.history.length > 0 &&
    // it must refer to at least two other events (start_event, previous_move)
    event.tags.filter((t) => t[0] === NIP01.TagEnum.e).length === 2
  )
}

export const isGameChatEvent = (gameId: NIP01.EventId, event?: NIP01.Event): boolean => {
  return (
    !!event &&
    event.kind === 1 &&
    event.tags.filter((t) => t[0] === NIP01.TagEnum.e).filter((t) => t[1] === gameId).length > 0
  )
}

export const gameIdToRookrId = (gameId: NIP01.EventId): RookrId => {
  const words = bech32m.toWords(hexToBytes(gameId))
  const encoded = bech32m.encode(Rookr_ID_PREFIX, words) as RookrId
  return encoded
}
export const RookrIdToGameId = (RookrId: RookrId): NIP01.EventId => {
  const decoded = bech32m.decode(RookrId)
  if (decoded.prefix !== Rookr_ID_PREFIX) {
    throw new Error('Cannot decode RookrId: invalid prefix')
  }

  const numbers = bech32m.fromWords(decoded.words)
  return bytesToHex(Uint8Array.of(...numbers))
}

export const tryParseRookrId = (possibleRookrId?: unknown): RookrId | null => {
  if (!possibleRookrId) return null

  const hasValidPrefix = String(possibleRookrId).startsWith(Rookr_ID_PREFIX + '1')
  if (hasValidPrefix === false) return null

  try {
    // @ts-ignore
    const unusedOnPurpose = RookrIdToGameId(possibleRookrId as RookrId)
    return possibleRookrId as RookrId
  } catch (e) {
    console.debug('Could not parse RookrId from given string')
    return null
  }
}
