import { useEffect, useState } from 'react'
import { useParams, useNavigate } from 'react-router-dom'

import { RookrId, tryParseRookrId } from '../util/Rookr'

interface RedirectToGameProps {
  RookrId?: RookrId
  replace: boolean
}
// TODO: this whole component is a hack.. generally works, but please try to remove it
export default function RedirectToGame({ RookrId: argRookrId, replace }: RedirectToGameProps) {
  const navigate = useNavigate()

  const { RookrId: paramsRookrId } = useParams<{ RookrId: RookrId | undefined }>()

  const [RookrId] = useState<RookrId | undefined>(
    tryParseRookrId(argRookrId) || tryParseRookrId(paramsRookrId) || undefined
  )

  useEffect(() => {
    if (!RookrId) return

    navigate(`/game/${RookrId}`, { replace })
  }, [navigate, RookrId, replace])

  return <></>
}
